.main__newsPage, .newsPage {
    background-color: black;
    min-height: calc(100vh - 80px);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    color: white;
    position: relative;
    padding-top: 20px;
    &:after {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        background-color: rgba(0,0,0,0.6);
        content: ""; }
    &__container {
        width: 90%;
        text-align: center;
        margin: 0 auto;
        position: relative;
        z-index: 1;
        @media (min-height: 1024px) {
            margin-top: 50px; }
        @media (min-width: 1200px) {
            margin-top: 120px; }
        .newsPage__activeMovieTitle {
            font-size: 30px;
            margin-bottom: 5px;
            @media (min-width: 768px) {
                font-size: 40px; }
            @media (min-width: 1024px) {
                font-size: 50px; } }
        .newsPage__activeMovieDesc {
            font-size: 12px;
            width: 90%;
            font-weight: normal;
            margin: 0 auto;
            @media (min-width:768px) {
                font-size: 18px; }
            @media (min-width: 1024px) {
                font-size: 24px; } }
        .newsPage__button {
            border: none;
            margin-top: 10px;
            background-color: rgba(51,51,51,0.8);
            color: white;
            height: 30px;
            width: 100px;
            font-size: 18px;
            border-radius: 5px;
            cursor: pointer;
            display: inline-block;
            @media (min-width: 1200px) {
                font-size: 24px;
                width: 120px; }
            &--more {
                margin-top: -10px;
                margin-right: 10px; }
            &--myList {
                margin-left: 10px; }
            &--link {
                text-decoration: none;
                color: white; } } }
    &__moviesContainer, .moviesContainer {
        height: 100px;
        width: 100vw;
        display: flex;
        flex-direction: row;
        margin: 20px auto 0 auto;
        position: absolute;
        bottom: 20px;
        left: 50%;
        transform: translateX(-50%);
        z-index: 1;
        @media (min-width: 568px) {
            position: relative;
            left: 0;
            transform: none;
            width: 90vw;
            margin-top: 50px; }
        @media (min-width: 667px) {
            width: 80vw; }
        @media (min-height: 1024px), (min-width: 1024px) {
            position: absolute;
            left: 50%;
            bottom: 20px;
            transform: translateX(-50%);
            height: 200px; }
        @media (min-width: 1200px) {
            width: 60%; }
        @media (min-width: 1600px) {
            width: 50%; }
        .moviesContainer__button {
            height: 20px;
            width: 20px;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            font-size: 24px;
            border: none;
            background-color: transparent;
            color: rgba(255,255,255,0.5);
            @media (min-width: 768px) {
                height: 30px;
                width: 24px;
                font-size: 30px; }
            &--back {
                left: 0; }
            &--next {
                right: 0; }
            &--active {
                color: white;
                cursor: pointer; } }
        .moviesContainer__movieList {
            height: 100%;
            width: calc(100% - 50px);
            position: absolute;
            top: 0;
            left: 25px;
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            overflow: hidden;
            .moviesContainer__singleItem {
                display: none;
                height: 100%;
                flex-basis: calc(25% - 4px);
                margin: 0 2px;
                overflow: hidden;
                cursor: pointer;
                &--active {
                    display: block; }
                .moviesContainer__movieImage {
                    height: 100%;
                    width: 100%; } } } } }
