.main__popularPage, .popularPage {
    min-height: calc(100vh - 80px);
    height: auto;
    background-color: #0B161A;
    padding-bottom: 20px;
    position: relative;
    &__title {
        margin: 0 auto;
        text-align: center;
        padding-top: 20px;
        color: white;
        font-size: 24px; }
    &__listContainer,.popularPage__listContainer .list {
        width: 90%;
        margin: 20px auto 12px auto;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        overflow: hidden;
        .list__singleItem {
            flex-basis: 100%;
            height: 100%;
            position: relative;
            display: none;
            @media (min-width: 768px) {
               display: block;
               flex-basis: 50%; }
            @media (min-width: 1024px) {
               flex-basis: calc(100% / 3); }
            @media (min-width: 1600px) {
               flex-basis: calc(100% / 4); }
            &--active {
                display: block; }
            .list__itemTitle {
                text-align: center;
                margin-top: 40px;
                font-weight: bold;
                color: white;
                font-size: 24px;
                @media (min-width: 1024px) {
                    margin-top: 50px;
                    font-size: 20px; } }

            .list__itemIndex {
                position: absolute;
                top: 20px;
                left: 20px;
                color: white;
                font-weight: bold;
                font-size: 18px; }
            .list__itemImageContainer {
                height: 240px;
                width: 240px;
                overflow: hidden;
                position: relative;
                margin: 20px auto 0 auto;
                background-color: #1B2124;
                border: 1px solid black;
                border-radius: 15px;
                .list__itemSingleImage {
                    height: 200px;
                    width: 200px;
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%,-50%); } }
            .list__button {
                border: none;
                margin-top: 10px;
                background-color: rgba(51,51,51,0.8);
                color: white;
                height: 30px;
                width: 100px;
                font-size: 18px;
                border-radius: 5px;
                cursor: pointer;
                display: block;
                margin: 10px auto;
                &--link {
                    text-decoration: none;
                    color: white; } } } }
    &__dotList, .dotList {
        position: absolute;
        bottom: 10px;
        left: 50%;
        transform: translateX(-50%);
        width: 90%;
        display: flex;
        flex-direction: row;
        overflow: hidden;
        height: 20px;
        @media (min-width: 568px) {
            width: 350px; }
        @media (min-width: 768px) {
            display: none; }
        @media (min-height: 812px) {
            bottom: 50px; }
        .dotList__singleItem {
            flex-basis: calc(100% / 20);
            overflow: hidden;
            .dotList__dotButton {
                font-size: 12px;
                border: none;
                background-color: transparent;
                color: white;
                border-radius: 15px;
                cursor: pointer;
                &--active {
                    background-color: white; } } } } }
